import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
/**
 * Generated class for the RegionManagerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-region-manager',
  templateUrl: 'region-manager.html',
})
export class RegionManagerPage {

  regionManagerAPIUrl = 'http://localiis/interactivedirectory/api/directory/GetProfitCenterDirectory/?view=3';

  people: any = [];
  itemExpandHeight: number = 50;
  constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http) {
    this.load();
  }

  load(){
    return this.http.get('../api/directory/GetProfitCenterDirectory/?view=2').map(res => res.json()).subscribe(data => {
      this.people = data;
      console.log(data);
    });
  }

  expandItem(person){
           this.people.map((listItem) => {
               if(person == listItem){
                   listItem.expanded = !listItem.expanded;
               } else {
                   listItem.expanded = false;
               }
               return listItem;
           });
       }
}



